<template>
  <div>
    <div class="single-blog">
      <!-- Services breadcrumb -->
      <section class="w3l-about-breadcrumb">
        <div class="breadcrumb-bg breadcrumb-bg-singleblog py-5">
          <div class="container py-lg-5 py-md-3 text-white">
            <h2>{{ blog.title }}</h2>
          </div>
        </div>
      </section>
      <!-- breadcrumb -->
      <div class="container py-3">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <router-link :to="{ name: 'Blogs' }">Blogs</router-link>
            </li>
            <li class="breadcrumb-item active" aria-current="page">
              {{ blog.title }}
            </li>
          </ol>
        </nav>
      </div>

      <section class="w3l-services">
        <div class="services1 py-lg-5 py-sm-3">
          <div class="container">
            <div class="row">
              <div class="col-lg-4">
                <img :src="blog.image" class="img-fluid rounded" alt="" />
              </div>
              <div class="col-lg-8">
                <h3 class="mt-3">{{ blog.title }}</h3>
                <p class="mt-3 text-justify">
                  {{ blog.description }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <!-- Call for action  -->
      <QuickContant />
    </div>
  </div>
</template>

<script>
// import axios from axios;
import QuickContant from "../components/helper/QuickContant.vue";
import b1 from "../../public/assets/images/publication.jpg";
export default {
  name: "SingleService",
  props: ["id"],
  components: { QuickContant },
  data() {
    return {
      blog: {},
      blogs: [],
    };
  },
  methods: {
    getBlog() {
      this.blogs.filter((blog) => {
        if (blog.id == this.id) {
          this.blog = {
            id: blog.id,
            title: blog.title,
            image: blog.image,
            description: blog.description,
          };
        }
      });
    },
  },
  created() {
    this.blogs = [
      {
        id: 1,
        title: "UNFPA  Comprehensive Midwifery Assessment ",
        image: b1,
        description: "",
      },
      {
        id: 2,
        title:
          "Develop a continental proposal and action plan for the re-strengthened implementation of the CARMMA campaign from 2021-2030 ",
        image: b1,
        description: "",
      },
      {
        id: 3,
        title:
          "Raleigh International - End of line Evaluation of Healthy Lives, Happy Homes",
        image: b1,
        description: "",
      },
    ];
    this.getBlog();
  },
};
</script>

